  const actions = {
    async nuevo(context, { firstname, email, lastname, de, files }){
        try {
          const formData = new FormData();

            // Agregar campos de texto al FormData
            formData.append("asunto", firstname);
            formData.append("mensaje", lastname);
            formData.append("para", email);
            formData.append("de", de);
            formData.append("files", files,);
          const { data, status } = await axios.instance.post("correos/enviar/", formData)
          const { message } = data
          return { "mensaje": message, "estado": status }
        } catch (error){
          return { "error": error }
          // return { "error": error.response.data.error, "mensaje": error.response.data.message }
        }
      },
  }
  
