<template>
    <v-menu bottom offset-y left>
        <template v-slot:activator="{ attrs, on }">
            <v-chip class="ma-2" v-bind="attrs" v-on="on">
                <v-avatar color="grey" left>
                    <v-img v-if="fotoUsuario !== null"
                        :src="$store.getters['MOD_autenticacion/getFotoUsuario']"
                    />
                    <v-icon v-else>{{ usernname[0] }} </v-icon>
                </v-avatar>
                {{ usernname }}
                <v-icon right>{{attrs["aria-expanded"] === "true" ? "mdi-menu-up" : "mdi-menu-down"}}</v-icon>
            </v-chip>
        </template>
        <v-list>
            <v-list-item link @click="redireccionar('personal')">
                <v-list-item-title>Administrar usuarios</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="cerrarSesion()">
                <v-list-item-title>Salir</v-list-item-title>
            </v-list-item>
        </v-list>
    </v-menu>
</template>

<script>
import { LOGOUT_FUNCTION } from "../../store/constants";
import { modulesWithLogoutAction } from "../../store/modules";

export default {
    computed: {
        fotoUsuario() {
        return this.$store.getters["MOD_autenticacion/getFotoUsuario"];
        },
        usernname() {
        return this.$store.getters["MOD_autenticacion/getNombreUsuario"];
        },
        rolUsuario() {
        return this.$store.getters["MOD_autenticacion/getUserPermissions"];
        },
    },
    methods: {
        async cerrarSesion() {
            await modulesWithLogoutAction.forEach((module) => {
                this.$store.dispatch(`${module}/${LOGOUT_FUNCTION}`);
            });
            this.$router.push({ name: "Login" });
        },
        redireccionar(nombre) {
            this.$router.push({
                name: nombre,
                params: { nombreUsuario: this.usernname },
            }).catch(() => {});
        }
    }
};
</script>
