<template>
  <div class="text-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="red lighten-2"
          dark
          v-bind="attrs"
          v-on="on"
        >
          Click Me
        </v-btn>
      </template>

      <v-card>
        <v-form ref="form"
        v-model="valid"
        lazy-validation>
          <v-container>
            <v-row>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="firstname"
                  :rules="nameRules"
                  label="Asunto"
                  required
                ></v-text-field>
              </v-col>
      
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="lastname"
                  :rules="nameRules"
                  label="Mensaje"
                  required
                ></v-text-field>
              </v-col>
      
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="email"
                  :rules="emailRules"
                  label="Para"
                  required
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="files"
                  label="Archivo"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-btn
      color="primary"
      @click="resetValidation"
    >
      Enviar correo
    </v-btn>
          </v-container>
        </v-form>
    </v-card>
    </v-dialog>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        dialog: false,
        valid: false,
      firstname: '',
      lastname: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => v.length <= 10 || 'Name must be less than 10 characters',
      ],
      email: 'nandoferinovizca@gmail.com',
      files: 0,
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
      }
    },
    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      async resetValidation () {
        // this.$refs.form.resetValidation()
        const {estado, mensaje,error} = await this.$store.dispatch("correo/nuevo",
        { firstname: this.firstname, email: this.email, lastname: this.lastname, de: 'nandoferinovizca@gmail.com', files:this.files})
        this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, });
      },
    },
  }
</script>