
import axios from "@/api"

const state = {

  verificarPeticiones:[],
  peticionesVerificadas: [],
  cargando: false,

  recurso: {},
  solicitud_recurso: {
    trabajos: [],
    tipo_pago: "",
    concepto_pago: "",
    pago_separado: false
  },
  recursos: [],

  comentarios: [],

  pago:{},
  pagos: [],

  archivo: {},
  archivos:[],

  solicitud_recurso_v2: {
    modalidad_pago: '',
    concepto_pago: '',
    banco: '',
    pago_separado: false,
    beneficiario: null,
    peticiones: [],
    transferencia: {
      tipo: null,
      transferencia:{
        clabe_interbancaria: ''
      },
      servicio:{
        convenio: '',
        referencia: '',
        linea_captura: ''
      },
      cuenta: {
        clabe: '',
        numero_cuenta: ''
      }
    },
    cheque:{}
  },
  solicitudes_recurso_v2: [],

  is_pago_separado: false
}

const mutations = {
  deleteRecurso(state, editedIndex){
    state.recursos.splice(editedIndex, 1)
  },
  setRecursos(state, payload){
    state.recursos = payload
  },
  cleanRecursos(state){
    state.recursos = []
  },
  cleanRecurso(state){
    state.recurso = {}
  },
  claerSolicitudRecurso(state){
    state.solicitud_recurso= {
      trabajos: [],
      tipo_pago: "",
      metodo_pago: "",
      pago_separado: false
    }
    state.verificarPeticiones=[]
    state.peticionesVerificadas=[]
  },
  cleanComentarios(state){
    state.comentarios = []
  },
  clearArchivos(state){
    state.archivos = []
  },
  deleteArchivo(state, indice){
    state.archivos.splice(indice, 1)
  },
  setVerificarPeticiones(state, payload){
    state.verificarPeticiones = payload
  },
  assignPeticionVerificada(state, {editar_index, editar_peticion}){
    state.peticionesVerificadas[editar_index] = Object.assign(state.peticionesVerificadas[editar_index], editar_peticion)
  },
  setPeticionesVerificadas(state, payload){
    state.peticionesVerificadas = payload
  },
  deleteVerificarPeticionesVerificadas(state, editarIndice){
    state.verificarPeticiones.splice(editarIndice, 1)
    state.peticionesVerificadas.splice(editarIndice, 1)
  },
  setRecurso(state, payload){
    state.recurso = payload
  },
  setSolicitudRecurso(state, payload){
    state.solicitud_recurso = payload
  },
  setSolicitudRecursoPagoSeparado(state, payload){
    state.solicitud_recurso.pago_separado = payload
  },
  assignSolicitudRecurso(state, payload){
    state.solicitud_recurso = Object.assign({}, state.solicitud_recurso, payload)
  },
  setComentarios(state, payload){
    state.comentarios = payload
  },
  addComentario(state, payload){
    state.comentarios.push(payload)
  },
  setPago(state, payload){
    state.pago = payload
  },
  setArchivos(state, payload){
    state.archivos = payload
  },
  addArchivo(state, payload) {
    state.archivos.push(payload)
  },
  setPagos(state, payload){
    state.pagos = payload
  },
  setCargando(state, payload){
    state.cargando = payload
  },
  setPeticionesSolicitudRecurso(state, payload){
    state.solicitud_recurso_v2.peticiones = payload
  },
  setModalidadPagoSolicitudRecurso(state, payload){
    state.solicitud_recurso_v2.modalidad_pago = payload
  },
  setConceptoPagoSolicitudRecurso(state, payload){
    state.solicitud_recurso_v2.concepto_pago = payload
  },
  setTipoTransferenciaSolicitudRecurso(state, payload){
    state.solicitud_recurso_v2.transferencia.tipo = payload
  },
  pushSolicitudesRecursoV2(state, payload){
    state.solicitudes_recurso_v2.push(payload)
  },
  setSolicitudesRecursoV2Atributo(state, {indice, key, valor}){
    state.solicitudes_recurso_v2[indice][key] = valor
  },
  setSolicitudesRecursoV2(state, payload){
    state.solicitudes_recurso_v2 = payload
  },
  deleteSolicitudesRecursoV2(state, editarIndice){
    state.solicitudes_recurso_v2.splice(editarIndice, 1)
  },
  setIsPagoSeparadoSeleccionado(state, payload){
    state.is_pago_separado = payload
  },
  clearSolicitudRecursoModalidadPagoTipoTransferencia(state){
    state.solicitud_recurso_v2.transferencia.transferencia.clabe_interbancaria = ''
  },
  clearSolicitudRecursoModalidadPagoTipoServicio(state){
    state.solicitud_recurso_v2.transferencia.servicio.convenio = ''
    state.solicitud_recurso_v2.transferencia.servicio.referencia = ''
    state.solicitud_recurso_v2.transferencia.servicio.linea_captura = ''
  },
  clearSolicitudRecursoModalidadPagoTipoCuenta(state){
    state.solicitud_recurso_v2.transferencia.cuenta.clabe = ''
    state.solicitud_recurso_v2.transferencia.cuenta.numero_cuenta = ''
  }
}

const actions = {
  async recuperarRecursos({commit}, { request, tipo_pago, validado }){
    try {
      commit("cleanRecursos")
      commit("setCargando", true)
      const recursos = []
      const { data, status } = await axios.instance.get(request ? `peticiones/recursos/?request=${request}`: `peticiones/recursos/?modalidad_pago=${tipo_pago}&validado=${validado}`)
      const { items, message } = data
      items.forEach(element => {
        console.log(element.beneficiary)
        recursos.push({
          "id": element.id,
          "tipo_pago": `${element.payment_mode} (${element.transfer_type})`,
          'datos_transferencia': element.transfer_data,
          "importe": element.total_amout,
          "creado": element.created_date,
          "banco": element.bank,
          'beneficiario': element.beneficiary,
          "datos_bancarios": element.bank_data,
          "solicitud": element.request,
          "validado": element.validate,
          'pagado': element.paid,
          "concepto": element.concept
        })
      });
      commit("setRecursos", recursos)
      commit("setCargando", false)
      return { "mensaje": message, "estado": status}
    } catch (error) {
      commit("setCargando", false)
      return { "error": error }
    }
  },
  async recuperarRecursosUsuario({commit}, { solicitado, gestor, solicitud, tipo_pago }){
    try {
      commit('cleanRecursos')
      commit('setCargando', true)
      var url_recurso = 'peticiones/recursos/trabajo/'
      if(solicitado){
        url_recurso = url_recurso + `?solicitado=${solicitado}`
      } else {
        url_recurso = url_recurso + (tipo_pago!==undefined ? `?gestor=${gestor}&tipo_pago=${tipo_pago}` : `?gestor=${gestor}&request=${solicitud}`) 
      }
      const recursos = []
      const { data, status } = await axios.instance.get(url_recurso)
      const { items, message } = data
      items.forEach(element => {
        recursos.push({
          'id': element.id,
          'num_trabajos': element.petitions.length,
          'tipo_pago': element.payment_mode === 'transferencia' ? `${element.payment_mode} (${element.transfer_type})` : element.payment_mode,
          'datos_transferencia': element.transfer_data,
          'banco': element.bank,
          'beneficiario': element.beneficiary,
          'importe': element.total_amout,
          'creado': element.created_date,
          'solicitud': element.request,
          'validado': element.validate,
          'concepto': element.concept
        })
      });
      commit('setRecursos', recursos)
      commit('setCargando', false)
      return { 'mensaje': message, 'estado': status}
    } catch (error) {
      commit('setCargando', false)
      return { 'error': error }
    }
  },
  async recuperarRecurso({commit}, id){
    try {
      commit('setCargando', true)
      const { data } = await axios.instance.get(`peticiones/recursos/${id}/`)
      const { items, message } = data
      const recurso = {
        'id': items.id,
        'creado': items.created_date,
        'importe': items.total_amout,
        'banco': items.bank,
        'tipo_pago': items.type_pay,
        'datos_transferencia': 'items transfer_data',
        'solicitud': items.request,
        'concepto': items.concept,
        'solicitado': items.request,
        'validado': items.validate,
        'peticiones': []
      }
      for(const peticion of items.petitions){
        recurso.peticiones.push({
          'trabajo':{
            'id': peticion.work.id,
            'concepto': peticion.work.concept,
            'gestor': peticion.work.area_user,
            'solicitado': peticion.work.assigned_user
          },
          'inmueble': {
            'cliente': peticion.work.property_office.client,
            'nombre': `${peticion.work.property_office.name} (${peticion.work.property_office.property_key})`,
            'estado': peticion.work.property_office.province,
            'municipio': peticion.work.property_office.municipality
          },
          'importe': peticion.amount
        })
      }
      commit('setCargando', false)
      commit('setRecurso', recurso)
      return { 'mensaje': message }
    } catch (error) {
      commit('setCargando', false)
      return { 'mensaje': error.response.data.message }
    }
  },
  async crearRecurso({state,}){
    try {
      const { is_pago_separado, solicitud_recurso_v2, solicitudes_recurso_v2 } = state
      const url = is_pago_separado ? "peticiones/recursos/multiple/" : "peticiones/recursos/"
      let recursos = []
      let recurso = {}
      if(is_pago_separado){
        for(let recurso of solicitudes_recurso_v2){
          const { modalidad_pago, transferencia, banco, concepto_pago, peticiones, beneficiario } = recurso
          const tipo_transferencia = modalidad_pago === 'transferencia' ? transferencia.tipo : null
          const datos_tipo_transferencia = modalidad_pago === 'transferencia' ? transferencia[tipo_transferencia] : {}
          recursos.push({
            'modalidad_pago': modalidad_pago,
            'tipo_transferencia': tipo_transferencia,
            'datos_tipo_transferencia': datos_tipo_transferencia,
            'banco': banco,
            'beneficiario': beneficiario.id,
            'concepto': concepto_pago,
            'trabajos': peticiones
          })
        }
      } else {
        const { modalidad_pago, transferencia, banco, concepto_pago, peticiones, beneficiario } = solicitud_recurso_v2
        const tipo_transferencia = modalidad_pago === 'transferencia' ? transferencia.tipo : null
        const datos_tipo_transferencia = modalidad_pago === 'transferencia' ? transferencia[tipo_transferencia] : {}
        recurso = {
          'modalidad_pago': modalidad_pago,
          'tipo_transferencia': tipo_transferencia,
          'datos_tipo_transferencia': datos_tipo_transferencia,
          'banco': banco,
          'beneficiario': beneficiario.id,
          'concepto': concepto_pago,
          'trabajos': peticiones
        }
      }
      console.log(recurso, recursos)
      const {data, status} = await axios.instance.post(url, is_pago_separado ? {'solicitudes_recursos': recursos} : recurso )
      const { message } = data
      return { "mensaje": message, "estado": status }
    } catch (error) {
      return { "error": error }
    }
  },
  async confirmarValidarRecuruso({commit}, {id, confirmacion, validacion, indice}){
    try {
      commit("setCargando", true)
      const confirmacion_validacion_recuro = confirmacion ? { "request": confirmacion } : { "validate": validacion }
      const {data, status} = await axios.instance.patch(`peticiones/recursos/${id}/`, confirmacion_validacion_recuro)
      const { message} = data

      commit("deleteRecurso", indice)
      commit("setCargando", false)
      return { "mensaje": message, "estado": status }
    } catch (error) {
      commit("setCargando", false)
      return { "error": error }
    }
  },
  async eliminarSolicitudRecurso({commit}, {id, indice}){
    try {
      commit("setCargando", true)

      const { data, status } = await axios.instance.delete(`peticiones/recursos/${id}/`)
      const { message } = data
      commit("deleteRecurso", indice)

      commit("setCargando", false)
      return { "mensaje": message, "estado": status }
    } catch (error) {
      commit("setCargando", false)
      return { "error": error}
    }
  },
  async deletePeticion({commit}, {id, editedIndex}){
    try {
      await axios.instance.delete(`peticiones/peticion/${id}/`)
      commit("deletePeticion", editedIndex)
      return { "mensaje": "Eliminación exitosa." }
    } catch (error) {
      return { "error": "404", "message": "No se elimino el pago." }
    }
  },
  async verificarPeticiones({commit}, peticiones){
    try {
      const verificar_peticiones = []
      const peticiones_verificadas = []
      for(const peticion of peticiones){
        verificar_peticiones.push({
          "work": peticion.trabajo,
          "amount": peticion.monto
        })
      }
      const { data, status } = await axios.instance.post("peticiones/peticion/verificar_peticiones/", {"works": verificar_peticiones})
      const { message, items } = data
      items.forEach(element => {
        if(element.error){
          peticiones_verificadas.push({"error": "Este trabajo ya fue mandado a solicitud de recursos"})
        } else {
          peticiones_verificadas.push({
            "trabajo": element.work_id,
            "monto": element.amount
          })
        }
      });
      commit("setPeticionesVerificadas", peticiones_verificadas)
      return { "mensaje": message, "estado": status }
    } catch (error) {
      return { "error": error }
    }
  },
  async confirmarPeticiones(context, peticiones){
    try {
      const works = []
      const peticionesConfirmadas = []
      for(const peticion of peticiones){
        if(!peticion.error){ works.push({"work": peticion.trabajo, "amount": peticion.monto})}
      }
      const { data, status } = await axios.instance.post("peticiones/peticion/confirmar_peticiones/", {"works": works})
      const { message, items } = data
      items.forEach((element) => {
        if(!element.error){
          peticionesConfirmadas.push({"id": element.id, "trabajo": element.work, "monto": element.amount})
        }
      });
      return { "mensaje": message, "estado": status, "peticiones": peticionesConfirmadas }
    } catch (error) {
      return { "error": error }
    }
  },
  async subirArchivo({commit}, { archivos, recurso, usuario }) {
    try {
      commit("setCargando", true)
      const formFile = new FormData()
      formFile.append("resource", recurso)
      formFile.append("changed_by", usuario)
      for(const archivo of archivos){
        formFile.append("files", archivo)
      }
      const { data, status } = await axios.instance.post("peticiones/comprobante/", formFile)
      const { items, message } = data
      for(const archivo_n in items){
        const archivo = {
          "id": archivo_n.id,
          "usuario": archivo_n.changed_by,
          "archivo": archivo_n.file
        }
        commit("addArchivo", archivo)
      }
      commit("setCargando", false)
      return { "mensaje": message, "estado": status }
    } catch (error) {
      commit("setCargando", false)
      return { "error": error }
    }
  },
  async recuperarArchivoRecurso({commit}, id){
    try{
      commit("setCargando", true)
      const { data, status } = await axios.instance.get(`peticiones/comprobante/?resource=${id}`)
      const { items, message } = data
      const archivos = []
      for(const archivo of items){
        archivos.push({
          "id": archivo.id,
          "archivo": archivo.file
        })
      }
      commit("setArchivos", archivos)
      commit("setCargando", false)
      return { "mensaje": message, "estado": status }
    } catch(error){
      commit("setCargando", false)
      return { "error": error }
    }
  },
  async eliminarArhivoRecurso({commit}, { id, indice }){
    try {
      commit("setCargando", true)
      const { data, status } = await axios.instance.delete(`peticiones/comprobante/${id}/`)
      const { message } = data
      commit("deleteArchivo", indice)
      commit("setCargando", false)
      return { "mensaje": message, "estado": status }
    } catch (error) {
      commit("setCargando", false)
      return {"error": error}
    }
  },
  async getPagos({commit}){
    try {
      const { data } = await axios.instance.get("peticiones/pagos/")
      const { items } = data
      const pagos = []
      items.forEach(element => {
        pagos.push({
          "id": element.id,
          "nombre": element.name,
          "total": element.total,
          "recipo": element.receipt,
          "creado": element.created_date
        })
      });
      commit("setPagos", pagos)
      return { "error": null }
    } catch (error) {
      return { "error": "Consulta satisfactoria." }
    }
  },
  async getPago(context, id){
    try {
      const { data } = await axios.instance.get(`peticiones/pagos/${id}/`)
      const { items } = data
      window.open(items.receipt.file)
    } catch (error) {
      return { "error": "Consulta satisfactoria." }
    }
  },
  async recuperarComentariosRecurso({commit}, id){
    try{
      commit("setCargando", true)
      const { data, status } = await axios.instance.get(`peticiones/comentario/?resource=${id}`)
      const { items, message } = data
      const comentarios = []
      for(const comentario of items){
        comentarios.push({
          "id": comentario.id,
          "usuario": comentario.changed_by,
          "comentario":comentario.comment
        })
      }
      commit("setComentarios", comentarios)
      commit("setCargando", false)
      return { "mensaje": message, "estado": status }
    } catch(error){
      commit("setCargando", false)
      return { "error": error }
    }
  },
  async hacerComentario({commit}, { usuario, recurso, comentario}){
    try {
      const { data, status } = await axios.instance.post("peticiones/comentario/", { "resource": recurso, "changed_by": usuario, "comment": comentario })
      const { items, message } = data
      const nuevo_comentario = {
        "id": items.id, "comentario": items.comment, "usuario": items.changed_by
      }
      commit("addComentario", nuevo_comentario)
      return { "mensaje": message, "estado": status }
    } catch (error) {
      return { "error": error }
    }
  },
  async inicializarAsignacionPeticionesSolicitudRecurso({state, commit}){
    let peticiones = []
    for(let peticion_verificada of state.peticionesVerificadas){
      peticiones.push(peticion_verificada)
    }
    commit('setPeticionesSolicitudRecurso', peticiones)
  },
  async inicializarAsignacionPeticionesSolicitudesRecurso({state, commit}){
    let solicitudes_recurso_v2 = []
    
    for(let peticion_verificada of state.peticionesVerificadas){
      let solicitud_recurso_v2= {
        modalidad_pago: '',
        concepto_pago: '',
        pago_separado: false,
        banco: '',
        beneficiario: null,
        peticiones: [peticion_verificada],
        transferencia: {
          tipo: '',
          transferencia:{
            clabe_interbancaria: ''
          },
          servicio:{
            convenio: '',
            referencia: '',
            linea_captura: ''
          },
          cuenta: {
            clabe: '',
            numero_cuenta: ''
          }
        },
        cheque:{}
      }
      solicitudes_recurso_v2.push(solicitud_recurso_v2)
    }
    commit('setSolicitudesRecursoV2', solicitudes_recurso_v2)
  }
}

const getters = {
  getRecursos:(state) => state.recursos,
  getVerificarPeticiones:(state) => state.verificarPeticiones,
  getPeticionesVerificadas:(state) => state.peticionesVerificadas,
  getSolicitudRecurso:(state) => state.solicitud_recurso,
  getRecurso: (state) => state.recurso,
  getComentarios: (state) => state.comentarios,
  getArchivo:(state) => state.archivo,
  getArchivos: (state) => state.archivos,
  getPago:(state) => state.pago,
  getPagos:(state) => state.pagos,
  getCargando:(state) => state.cargando,
  getSolicitudRecursoV2:(state) => state.solicitud_recurso_v2,
  getIsPagoSeparadoSeleccionado:(state) => state.is_pago_separado,
  getSolicitudesRecursoV2:(state) => state.solicitudes_recurso_v2
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}