<template>
    <v-dialog v-model="is_dialogo" width="auto">
        <template v-slot:activator="{on, attrs}">
            <span v-on="on" v-bind="attrs">Gestionar privilegios</span>
        </template>
        <v-card>
            <v-card-title class="grey lighten-2">Privilegios de {{usuario.nombre}}
                <v-spacer></v-spacer>
                <v-btn icon color="error" @click="is_dialogo=false"><v-icon>mdi-close</v-icon></v-btn>
            </v-card-title>
            <v-card-text v-if="grupos.length">
                <v-row dense>
                    <v-col v-for="privilegio of privilegios" :key="privilegio.id" xs="12" sm="12" md="12" lg="12" xl="12">
                        <v-switch
                            v-model="privilegio_usuario"
                            :label="privilegio.nombre"
                            inset
                            :value="privilegio.id"
                            @change="setPrivilegioPersonal(privilegio)"
                        />
                    </v-col>
                </v-row>
            </v-card-text>
            <v-card-text v-else class="text--primary container">
                <br>
                <span>No hay una lista de privilegios para asignar al usuario.</span>
            </v-card-text>
            <v-card-actions>
                <v-spacer />
                <v-btn :disabled="grupos.length ? false : true" color="error" class="ma-2" outlined @click="is_dialogo=false">Cancelar</v-btn>
                <v-btn :disabled="grupos.length ? false : true" color="success" outlined @click="asignarPrivilegios()">Asignar</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    props:{
        usuario:{ type: Object, default:()=>({}) },
    },
    data:()=> ({
        dialogo: false
    }),
    computed:{
        is_dialogo: {
            get(){ return this.dialogo },
            set(nuevoValor){
                this.dialogo = nuevoValor
            }
        },
        grupos() {
            const grupos_usuario = []
            const { grupos } = this.usuario
            for(const grupo of this.$store.getters['grupo/getGrupos']){
                let is_active = grupos.includes(grupo.id) ? true : false
                grupos_usuario.push({'id': grupo.id, 'nombre': grupo.nombre, is_active: is_active})
            }
            return grupos_usuario
        },
        privilegios() {
            return this.$store.getters['grupo/getGrupos']
        },
        privilegio_usuario: {
            get(){ return this.usuario.grupos },
            set(nuevoValor){
                if(!Array.isArray(nuevoValor)){
                    if(this.usuario.grupos.includes(nuevoValor)){
                        let index = this.usuario.grupos.indexOf(nuevoValor)
                        this.usuario.grupos.splice(index, 1)
                    } else {
                        this.usuario.grupos.push(nuevoValor)
                    }
                }
            }
        }
    },
    methods:{
        setPrivilegioPersonal(privilegio){
            this.privilegio_usuario = privilegio.id
        },
        async asignarPrivilegios(){
            const { estado, mensaje, error } = await this.$store.dispatch('usuario/actulizarPrivilegiosPersonal', this.usuario)
            this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        }
    },
}
</script>