import axios from "@/api";

const state = {
  beneficiario: {
    nombre: '',
    numero_cuenta: null,
    banco: ''
  },
  beneficiarios: [],
  cargando: false
}

const mutations = {
  setBeneficiario(state, payload){
    state.beneficiario = payload
  },
  setBeneficiarios(state, payload){
    state.beneficiarios = payload
  },
  addBeneficiario(state, payload){
    state.beneficiarios.push(payload)
  },
  setCargando(state, payload){
    state.cargando = payload
  },
  deleteBeneficiario(state, payload){
    state.beneficiarios.splice(payload, 1)
  },
  clearBeneficiario(state){
    state.beneficiario = {
      nombre: '',
      numero_cuenta: null,
      banco: ''
    }
  }
}

const actions = {
  async obtenerBeneficiarios({commit}){
    try {
      const { data, status } = await axios.instance.get('beneficiarios/beneficiario/')
      const { items, message } = data;
      let beneficiarios = []
      for(const beneficiario of items){
        beneficiarios.push({id: beneficiario.id, nombre: beneficiario.name, banco: beneficiario.bank, numero_cuenta: beneficiario.interbank_code})
      }
      // const beneficiarios = [
      //   { nombre: 'Pepe', banco: 'BBVA', 'numero_cuenta': 1234567890 },
      //   { nombre: 'Municipio las brisas', banco: 'BBVA', 'numero_cuenta': "n/a" },
      // ]
      commit('setBeneficiarios', beneficiarios)
      return { mensaje: message, estado: status }
    } catch (error) {
      return { 'error': error }
    }
  },
  async crearBeneficiario({commit}, { nombre, numero_cuenta, banco}){
    try {
      const { data, status } = await axios.instance.post('beneficiarios/beneficiario/', {
        'name': nombre,
        'interbank_code': numero_cuenta,
        'bank': banco
      })
      const { message, items } = data
      const { id, name, bank, interbank_code } = items
      const beneficiario = {
        'id': id,
        'nombre': name,
        'banco': bank,
        'numero_cuenta': interbank_code
      }
      commit('setBeneficiario', beneficiario)
      commit('addBeneficiario', beneficiario)
      return { mensaje: message, estado: status }
    } catch (error) {
      return { 'error': error }
    }
  },
  async actualizarBeneficiario({commit}, {beneficiario}){
    try {
      commit('setBeneficiario', beneficiario)
      return { 'mensaje': 'ok', 'estado': 200 }
    } catch (error) {
      return { 'error': error }
    }
  },
  async eliminarBeneficiario({commit}, { editar_index }){
    try {
      commit('deleteBeneficiario', editar_index)
      commit('clearBeneficiario')
      return { 'mensaje': 'ok', 'estado': 200 }
    } catch (error) {
      return { 'error': error }
    }
  }
}

const getters = {
  getBeneficiarios:(state) => state.beneficiarios,
  getBeneficiario: (state) => state.beneficiario
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}
