<template>
    <v-row justify="center">
        <v-col cols="12" sm="2">
            <v-sheet color="grey lighten-3" class="pa-4">
                <h1><b>Recursos</b></h1>
            </v-sheet>
            <v-list>
                <v-list-item-group>
                    <v-list-item link @click="inicializarRecursosUsuario()">
                        <v-list-item-content>
                            <v-list-item-title>Solicitudes de Recursos</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>n</v-list-item-action>
                    </v-list-item>
                </v-list-item-group>
                <v-list-group no-action>
                    <template v-slot:activator>
                        <v-list-item-content>
                            <v-list-item-title>Peticiones</v-list-item-title>
                        </v-list-item-content>
                    </template>
                    <v-list-item link @click="inicializarRecursos({tipo_pago:'Transferencia', is_confirmar_recurso: false})">
                        <v-list-item-content >
                            <v-list-item-title>Transferencias</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>n</v-list-item-action>
                    </v-list-item>
                    <v-list-item link @click="inicializarRecursos({tipo_pago:'Cheque', is_confirmar_recurso: false})">
                        <v-list-item-content >
                            <v-list-item-title>Cheques</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>n</v-list-item-action>
                    </v-list-item>
                </v-list-group>
            </v-list>
        </v-col>
        <v-col cols="12" sm="8">
            <peticiones
                :isConfirmarRecurso="isConfirmarRecurso"
                :showInputFile="false"
                :addHeaders="headers"
                :titulo="titiulo"
            />
        </v-col>
    </v-row>
</template>

<script>
import Peticiones from '../../components/Recurso/TablaSolicitudesRecursos.vue'

export default {
    data: () => ({
        isConfirmarRecurso: true,
        headers:[],
        titiulo: ''
    }),
    components: {
        peticiones: Peticiones
    },
    computed:{
        usuario() { return this.$store.getters['MOD_autenticacion/obtenerIdUsuario'] },
    },
    created(){
        this.inicializarRecursosUsuario()
        // this.inicializarRecursos({solicitud:'False', is_confirmar_recurso: true})
    },
    methods: {
        async inicializarRecursos({solicitud, tipo_pago, is_confirmar_recurso }) {
            this.titiulo = 'Proceso de Validación y Confirmación de Pagos en Solicitudes de Recursos'
            this.headers= [
               { text: 'Solicitud de recursos', value: 'actions', align:'center' }
            ]
            this.isConfirmarRecurso=is_confirmar_recurso
            const { mensaje, estado, error } = await this.$store.dispatch('recurso/recuperarRecursosUsuario', {
                solicitud: solicitud, gestor: this.usuario, tipo_pago: tipo_pago
            })
            if(error) {
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error, })
            }
        },
        async inicializarRecursosUsuario(){
            this.headers = [
                {text: 'Validado', value: 'validado', align: 'center'},
                {text: 'Pagado', value: 'pagado', align: 'center'},
                {text: 'Reembolsado | Comprobado', value: 'RemOrCom', align: 'center'}
            ]
            this.titiulo = 'Seguimiento de Solicitudes de Recursos'
            const { mensaje, estado, error } = await this.$store.dispatch('recurso/recuperarRecursosUsuario', { solicitado: this.usuario });
            if (error) {
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error, });
            }
        }
    }
}
</script>