import axios from '@/api'

import { localStorage2json } from '@/utils/data/store'

// import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from '@/store/constants'

export default {
  namespaced: true,

  state: {
    usuario: {
      id: -1,
      nombre: '',
      apellidos: '',
      nombre_usuario: '',
      correo: '',
      pwd: '',
      is_activo: false,
      is_personal: false,
      grupos: []
    },
    usuarios: [],

    mensaje: '',

    is_cargando: false
  },

  mutations: {
    setUsuario(state, payload){ state.usuario = payload },
    setUsuarios(state, resultado){ state.usuarios = resultado },
    cleanUsuarios(state) { state.usuarios = [] },
    addUsuario(state, payload){ state.usuarios.push(payload) },
    updateUsuarioPermisos(state, {indice, is_activo, is_personal}){
      state.usuarios[indice].is_activo = is_activo
      state.usuarios[indice].is_personal = is_personal
    },
    updatePrivilegiosPersonal(state, {id, grupos}){
      let usuario = state.usuarios.find(usuario => usuario.id === id)
      let indice = state.usuarios.indexOf(usuario)
      state.usuarios[indice].grupos = grupos
    },
    setCargando(state, payload){ state.is_cargando = payload },
  },

  actions: {
    // [INICIALIZAR_FUNCIONES]({ dispatch }) {
    //   dispatch('iniciarAutenticacion')
    // },
    // [LOGOUT_FUNCTION]({ commit }) {
    //   commit('limpiarTokens')
    // },
    // iniciarAutenticacion({ commit }) {
    //   const asignaciones = localStorage2json('asignaciones')
    //   commit('setTrabajosAsignados', asignaciones)
    // },

    async obtenerUsuarios({ commit }, {privilegio}) {
      try {
        const { data, status } = await axios.instance.get(`usuarios/usuario/?grupo=${privilegio}`, {headers: {Authorization : `Bearer ${localStorage2json('token')}`}})
        const { items, message } = data
        var usuarios = []
        items.forEach((element) => {
          usuarios.push({
            id: element.id,
            nombre: element.name,
            apellidos: element.last_name,
            nombre_usuario: element.username,
            correo_electronico: element.email,
            imagen: element.image,
            is_activo: element.is_active,
            is_personal: element.is_staff,
            is_superusuario: element.is_superuser,
            ultimo_acceso: element.last_login,
            grupos: element.groups
          })
        })
        commit('setUsuarios', usuarios)
        return { 'mensaje': message, 'estado': status }
      } catch (error) {
        commit('cleanUsuarios')
        return { 'error': error }
      }
    },

    async crearUsuario({state, commit}) {
      try {
        commit("setCargando", true)
        const {usuario} = state
        const personal = {
          username: usuario.nombre_usuario,
          email: usuario.correo,
          password: usuario.pwd,
          name: usuario.nombre,
          last_name: usuario.apellidos,
          is_staff: true,
          groups: usuario.grupos
        }
        const headers = {Authorization : `Bearer ${localStorage2json('token')}`}
        const { data, status } = await axios.instance.post('/usuarios/usuario/', personal, {headers})
        const { message, items } = data
        const { id, username, email, name, last_name, image, is_active, is_staff, groups, is_superuser } = items
        const usuarios = {
          id: id,
          nombre: name,
          apellidos: last_name,
          nombre_usuario: username,
          correo_electronico: email,
          imagen: image,
          is_superusuario: is_superuser,
          is_activo: is_active,
          is_personal: is_staff,
          grupos: groups
        }
        commit("addUsuario", usuarios)
        commit("setCargando", false)
        return { mensaje: message, estado: status }
      } catch (error) {
        commit("setCargando", false)
        return { error: error }
      }
    },
    async actulizarPermisos({commit}, { id, indice, is_activo, is_personal}){
      try {
        const headers = {Authorization : `Bearer ${localStorage2json('token')}`}
        const { data, status } = await axios.instance.put(`usuarios/usuario/${id}/permisos/`, {
          is_active: is_activo, is_staff: is_personal
        }, {headers})
        const { items, message } = data
        const { is_active, is_staff } = items
        commit("updateUsuarioPermisos", { indice: indice, is_activo: is_active, is_personal: is_staff })
        return { mensaje: message, estado: status }
      } catch (error) {
        return { error: error }
      }
    },
    async actulizarPrivilegiosPersonal({commit}, {id, grupos}){
      try {
        const headers = {Authorization : `Bearer ${localStorage2json('token')}`}
        const { data, status } = await axios.instance.patch(`usuarios/usuario/${id}/grupos/`, { 'id': id, 'groups': grupos }, {headers})
        const { items, message } = data
        commit('updatePrivilegiosPersonal', {id: id, grupos: items.groups})
        return { mensaje: message, estado: status }
      } catch (error) {
        return { error: error }
      }
    },
    async desactivarUsuarioPersonal(constants, usuario_id){
      try {
        const headers = {Authorization : `Bearer ${localStorage2json('token')}`}
        const { data, status } = await axios.instance.delete(`usuarios/usuario/${usuario_id}/`, {headers})
        const { message } = data
        return { mensaje: message, estado: status }
      } catch (error) {
        return { error: error }
      }
    },
    async recuperarUsuario(context, id){
      try {
        const { data, status } = await axios.instance.get(`/usuarios/usuario/${id}/`)
        const { message } = data
        // commit('setUsuario', {items})
        return { mensaje: message, estado: status }
      } catch(error) {
        return { error: error }
      }
    },
  },
  getters: {
    getUsuarios: (state) => state.usuarios,
    getUsuario: (state) => state.usuario,
    getCargando:(state) => state.is_cargando
  },
}
