<template>
  <v-row no-gutters justify="center">
    <v-col cols="12" sm="8" md="8" lg="8" xl="8" xs="8">
      <v-card class="mx-auto" outlined elevation="5">
        <tabla-miembros
          :usuarios="usuarios"
          :cabeceras="cabeceras"
          @actualizarListaUsuario="listarUsuarios"
        />
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import TablaMiembros from '../../components/Usuario/TablaPersonal.vue'
export default {
  data: () => ({
    cabeceras: [
      {text: 'Correo', value: 'correo_electronico' },
      {text: 'Usuario', value: 'nombre_usuario'},
      {text: 'Nombre', value: 'nombre'},
      {text: 'Activo', value: 'is_activo', align:'center'},
      {text: 'Personal', value: 'is_personal', align:'center'},
      { text: 'Super usuario', value: 'is_superusuario', align:'center'},
      { text: 'Acciones', value: 'accion' }
    ]
  }),
  components: {
    "tabla-miembros": TablaMiembros
  },
  computed: {
    usuarios() { return this.$store.getters["usuario/getUsuarios"] }
  },
  created(){
    this.listarUsuarios()
  },
  methods:{
    async listarUsuarios(){
      const { mensaje, estado, error } = await this.$store.dispatch("usuario/obtenerUsuarios",{privilegio: ''})
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    }
  }
}
</script>