<template>
    <v-card>
        <v-card-title class="grey lighten-3">{{titulo}}</v-card-title>
        <v-card-subtitle class="grey lighten-3"></v-card-subtitle>
        <br>
        <v-card-text>
            <v-data-table
                :headers="headers"
                :items="recursos"
                loading-text="Cargando... por favor espere"
                :footer-props="{ itemsPerPageText: 'Filas por página', pageText: '' }"
                no-data-text="No tienes solicitudes de recursos pendientes"
                class="elevation-5 row-pointer"
                :loading="cargando"
                @click:row="recuperarRecurso"
            >
                <template v-if="(isConfirmarRecurso || isValidarRecurso)" v-slot:item.actions="{ item, index }">
                    <!-- <v-icon class="mr-2" color="success" @click="aceptarPeticion(item)">
                    mdi-check
                    </v-icon> -->
                    <confirmar v-if="isConfirmarRecurso"
                        mensaje="Estás a punto de confirmar la solicitud de recurso. ¿Estás seguro?"
                        :id="item.id"
                        :indice="index"
                        :tipo_pago="'¿Confirmar ' + item.tipo_pago + '?'"
                        @emitConfirmacion="confirmarRecurso"
                        icon="mdi-check"
                        color="success"
                    />
                    <confirmar v-if="isValidarRecurso"
                        mensaje="Estás a punto de verificar la solicitud de recurso. ¿Estás seguro?"
                        :id="item.id"
                        :indice="index"
                        :tipo_pago="'¿Verificar '+item.tipo_pago + '?'"
                        @emitConfirmacion="confirmarValidacion"
                        icon="mdi-check"
                        color="success"
                    />
                    <v-icon color="error">mdi-close</v-icon>
                </template>
                <template v-slot:item.holgura="{item}">
                    {{ holgura(item.creado) }}
                </template>
                <template v-slot:item.solicitud="{item}">
                    <v-icon :color="item.solicitud?'success':'error'">{{item.solicitud? 'mdi-checkbox-marked-circle' : 'mdi-close-circle'}}</v-icon>
                </template>
                <template v-slot:item.validado="{item}">
                    <v-icon :color="item.validado?'success':'error'">{{item.validado? 'mdi-checkbox-marked-circle' : 'mdi-close-circle'}}</v-icon>
                </template>
                <template v-slot:item.pagado="{item}">
                    <v-icon :color="item.pagado?'success':'error'">{{item.pagado? 'mdi-checkbox-marked-circle' : 'mdi-close-circle'}}</v-icon>
                </template>
                <template v-slot:item.RemOrCom>
                    <v-icon color="error" disabled>mdi-close-circle</v-icon>
                </template>
            </v-data-table>
            <recuperar-solicitud-recurso
                :dialog="dialogRecurso"
                :id_recurso="peticion"
                :nombre_archivo="isValidarRecurso?'Comprobante de validación':'Facturas'"
                :indice_recuros="indice"
                :showInputFile="showInputFile"
                @emitDialog="setDialogRecurso"
            />
        </v-card-text>
    </v-card>
</template>
<script>
import SolicitudRecurso from "../../views/Recurso/SolicitudRecurso.vue"
import Confirmar from "../../alertas/Confirmar.vue"
export default {
    props:{
        isConfirmarRecurso: { type: Boolean, default: false },
        isValidarRecurso: { type: Boolean, default: false },
        addHeaders: { tyepe:Object, default:() =>([])},
        showInputFile: { type: Boolean, default: true},
        titulo: { type: String, default: '' }
    },
    data: () => ({
        headers: [
            { text: "Fecha solicitado", value: "creado" },
            { text: "Dias", value: 'holgura'},
            { text: "Solicitado por", value: 'usuario_asignado' },
            { text: 'Trabajos', value: 'num_trabajos' },
            { text: "Modalidad de pago", value: "tipo_pago" },
            { text: 'Datos transferencia', value:'datos_transferencia'},
            { text: 'Banco', value: 'banco'},
            { text: 'Beneficiario', value: 'beneficiario'},
            { text: "Concepto", value: "concepto" },
            { text: "Importe", value: "importe" }
            // el tamaño debe ser el mismo para headers_length = 10
        ],
        dialogRecurso:false,
        peticion: -1,
        indice: -1,
        headers_length: 10
    }),
    components: {
        "recuperar-solicitud-recurso": SolicitudRecurso,
        "confirmar": Confirmar
    },
    mounted(){
        if(this.addHeaders.length){
            for(const header of this.addHeaders)
                this.headers.push(header)
        }
    },
    computed: {
        recursos() { return this.$store.getters["recurso/getRecursos"] },
        cargando() { return this.$store.getters["recurso/getCargando"] }
    },
    methods: {
        setDialogRecurso(nuevoValor){ this.dialogRecurso = nuevoValor },
        async confirmarRecurso({confirmacion, id, indice }) {
            if(confirmacion){
                const { mensaje, estado, error } = await this.$store.dispatch('recurso/confirmarValidarRecuruso', { 'id':id, 'confirmacion':confirmacion, 'indice':indice })
                this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
            }
        },
        async confirmarValidacion({confirmacion, id, indice }) {
            if(confirmacion){
                const { mensaje, estado, error } = await this.$store.dispatch('recurso/confirmarValidarRecuruso', { 'id':id, 'validacion':confirmacion, 'indice':indice })
                this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
            }
        },
        async recuperarRecurso({id}, {index}) {
            this.peticion=id
            this.dialogRecurso=true
            this.indice=index
            const { mensaje, estado, error } = await this.$store.dispatch("recurso/recuperarRecurso", id)
            if (error) {
                this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, })
            } else {
                const {error, estado, mensaje} = await this.$store.dispatch("recurso/recuperarComentariosRecurso", id)
                if (error) {
                this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, })
                }
                await this.$store.dispatch("recurso/recuperarArchivoRecurso", id );
            }
        },
        holgura(modified_date) {
            var date1 = new Date(modified_date)
            var date2 = new Date()
            var datestring = date2.getFullYear() + "-" + (date2.getMonth() + 1) + "-" + date2.getDate()
            var date3 = new Date(datestring)
            var holgura = (date3.valueOf() - date1.valueOf()) / 86400000
            // if (this.blue < holgura) this.blue = holgura
            return holgura
        }
    },
    watch:{
        addHeaders(evento){
            let splice_headers = this.headers.length - (this.headers.length-this.headers_length)
            this.headers.splice(splice_headers)
            if(evento.length){
                for(const header of evento)
                    this.headers.push(header)
            }
        }
    }
};
</script>

<style scoped>
::v-deep tr:hover {
  cursor: pointer;
}
</style>
