import axios from "@/api"

import { localStorage2json, json2localStorage } from "../../utils/data/store";

import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from "../constants"

export default {
  namespaced: true,

  state:{
    cliente:{},
    clientes: [],
    cargando: false,
    mensaje: "",
    isActivoFormularioCrearNuevoCliente: false
  },

  mutations:{
    setClientes(state, payload){
      json2localStorage("clientes", payload)
      state.clientes = payload
    },
    setCliente(state, payload){
      json2localStorage("cliente", payload)
      state.cliente = payload
    },
    setCargando(state, payload){ state.cargando = payload },
    setIsActivoFormularioCrearNuevoCliente(state, payload) { state.isActivoFormularioCrearNuevoCliente = payload },
    setMensaje(state, payload){ state.mensaje = payload },
    cleanModCliente(state){
      json2localStorage("clientes", [])
      json2localStorage("cliente", {})
      state.clientes = []
      state.cliente = {}
    },
    
    deleteCliente(state, indice){
      state.clientes.splice(indice, 1)
    }
  },

  actions:{
    [INICIALIZAR_FUNCIONES]({ dispatch }){
      dispatch("iniciarModCliente");
    },
    [LOGOUT_FUNCTION]({ commit }){
      commit("cleanModCliente");
    },
    iniciarModCliente({ commit },){
      const clientes = localStorage2json("clientes") ? localStorage2json("clientes") : []
      const cliente = localStorage2json("cliente") ? localStorage2json("cliente") :{}
      commit("setClientes", clientes)
      commit("setCliente", cliente)
    },

    async obtenerClientes({ commit }){
      try {
        commit("setCargando", true)
        const { data, status } = await axios.instance.get("/clientes/cliente/", { headers: {
            Authorization : `Bearer ${localStorage2json('token')}`
          }
        })
        const { items, message } = data
        var clientes = []
        items.forEach(element => {
          clientes.push({
            "id": element.id,
            "nombre": element.name,
            "rfc": element.rfc,
            "direccion": element.address
          })
        })
        commit("setClientes", clientes)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error){
        commit("cleanModCliente")
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async recuperarCliente({commit}, cliente){
      try {
        commit("setCliente", cliente)
        return { "mensaje": "Se pudo establecer el cliente.", "status": 200 }
      } catch (error){
        return { "mensaje": "No se establecio el cliente.", "error": "error", "status": 400 }
      }
    },

    async nuevo({ state, commit }, { nombre, direccion, rfc }){
      try {
        commit("setCargando", true)
        const cliente = {
          "name": nombre,
          "address": direccion,
          "rfc": rfc
        }
        const { data, status } = await axios.instance.post("clientes/cliente/", cliente)
        const { items, message } = data
        const nuevoCliente = { "nombre": items.name, "rfc":items.rfc, "id": items.id, "direccion": items.address }
        state.clientes.push(nuevoCliente)
        commit("setCliente", nuevoCliente)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error){
        commit("setCargando", false)
        return { "error": error }
        // return { "error": error.response.data.error, "mensaje": error.response.data.message }
      }
    },
    async eliminar({ commit }, { id, indice }){
      try {
        const { data } = await axios.instance.delete(`clientes/cliente/${id}/`)
        const { message } = data
        commit("deleteCliente", indice)
        return { "mensaje": message }
      } catch (error){
        return { "error": error.response.data.error, "mensaje": error.response.data.message }
      }
    },
  },

  getters:{
    getCliente:(state) => state.cliente,
    getClientes:(state) => state.clientes,
    getCargando:(state) => state.cargando,
    getIsActivoFormularioCrearNuevoCliente:(state) => state.isActivoFormularioCrearNuevoCliente,
    getMensaje:(state) => state.mensaje,
    isClientes:(state)=> Boolean(state.clientes.length)
  }
}