import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'

import { INICIALIZAR_FUNCIONES } from './store/constants';
import { modulesToInit } from './store/modules';

Vue.config.productionTip = false

modulesToInit.forEach((module) => {
  store.dispatch(`${module}/${INICIALIZAR_FUNCIONES}`)
});

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
