<template>
    <v-row dense>
        <v-col v-if="solicitud_recurso.modalidad_pago==='transferencia'" xs="12" sm="12" md="12" lg="12" xl="12">
            <formulario-tipo-transferencia :solicitud_recurso="solicitud_recurso"></formulario-tipo-transferencia>
        </v-col>
        <v-col xs="12" sm="12" md="12" lg="12" xl="12">
            <v-card outlined>
                <v-card-subtitle>Datos generales</v-card-subtitle>
                <v-card-text>
                    <v-row dense>
                        <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                            <v-text-field
                                v-model="banco"
                                label="Banco"
                                :rules="[(v) => !!v || 'Es necesario escribir el banco']"
                                outlined
                                clearable
                            />
                        </v-col>
                        <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                            <v-combobox
                                v-model="beneficiario"
                                :items="beneficiarios"
                                label="Beneficiario"
                                item-text="nombre"
                                :rules="[(v) => !!v || 'Es necesario elegir un beneficiario']"
                                clearable
                                clear-icon="mdi-close"
                                return-object
                                outlined
                            >
                            <template v-slot:append-outer>
                                <formulario-crear-beneficiario />
                              </template>
                            </v-combobox>
                        </v-col>
                        <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                            <v-textarea
                                v-model="concepto_pago"
                                outlined
                                label="Concepto de pago"
                                :rules="[(v) => !!v || 'Es necesario escribir el concepto de pago.', v =>  2 < v.length || 'Concepto de pago muy corto.']"
                                auto-grow
                                rows="1"
                                row-height="15"
                            />
                        </v-col>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import crearNuevoBeneficiario from "../../components/Beneficiario/CrearBeneficiario.vue"
import formularioTipoTransferencia from '../../components/Recurso/FormularioTipoTransferencia.vue'
export default {
    props: {
        solicitud_recurso: { typeof: Object, default:() => {} }
    },
    components: {
        "formulario-crear-beneficiario": crearNuevoBeneficiario,
        'formulario-tipo-transferencia': formularioTipoTransferencia
    },
    computed: {
        beneficiarios(){ return this.$store.getters["beneficiario/getBeneficiarios"] },
        beneficiario: {
            get(){ return this.solicitud_recurso.beneficiario },
            set(nuevoValor){
                this.solicitud_recurso.beneficiario = nuevoValor
            }
        },
        banco: {
            get(){ return this.solicitud_recurso.banco },
            set(nuevoValor){
                this.solicitud_recurso.banco = nuevoValor
            }
        },
        concepto_pago: {
            get(){ return this.solicitud_recurso.concepto_pago },
            set(nuevoValor){
                this.solicitud_recurso.concepto_pago = nuevoValor
            }
        }
    },
    created(){
        this.inicializarBeneficiarios()
    },
    methods: {
        async inicializarBeneficiarios(){
            await this.$store.dispatch("beneficiario/obtenerBeneficiarios")
        },
    }
};
</script>
