<template>
    <v-card outlined>
        <v-card-subtitle>{{ tipo_transferencia }} </v-card-subtitle>
        <v-card-text>
            <v-row v-if="solicitud_recurso['transferencia'].tipo === 'transferencia'" dense>
                <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                    <v-text-field
                        v-model="solicitud_recurso['transferencia']['transferencia']['clabe_interbancaria']"
                        outlined
                        label="Clabe interbancaria"
                        counter="18"
                        type="number"
                        :rules="reglas_clabe_interbancaria"
                    />
                </v-col>
            </v-row>
            <v-row v-if="solicitud_recurso['transferencia'].tipo === 'cuenta'" dense>
                <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                    <v-text-field
                        v-model="solicitud_recurso['transferencia']['cuenta']['clabe']"
                        outlined
                        label="Numero de tarjeta"
                        counter="16"
                        type="number"
                        :rules="reglas_clabe"
                    />
                </v-col>
                <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                    <v-text-field
                        v-model="solicitud_recurso['transferencia']['cuenta']['numero_cuenta']"
                        outlined
                        label="Numero cuenta"
                        counter="10"
                        type="number"
                        :rules="reglas_numero_cuenta"
                    />
                </v-col>
            </v-row>
            <v-row v-if="solicitud_recurso['transferencia'].tipo === 'servicio'" dense>
                <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                    <v-text-field
                        v-model="solicitud_recurso['transferencia']['servicio']['convenio']"
                        outlined
                        label="Pago por convenio"
                        counter="20"
                        type="number"
                        :rules="reglas_convenio"
                    />
                </v-col>
                <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                    <v-text-field
                        v-model="solicitud_recurso['transferencia']['servicio']['referencia']"
                        outlined
                        counter="30"
                        label="Pago por referencia"
                        type="number"
                        :rules="reglas_referencia"
                    />
                </v-col>
                <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                    <v-text-field
                        v-model="solicitud_recurso['transferencia']['servicio']['linea_captura']"
                        outlined
                        label="Liena de captura"
                        counter="20"
                        type="number"
                        :rules="reglas_linea_captura"
                    />
                </v-col>
            </v-row>
        </v-card-text>
    </v-card>
</template>
<script>
export default {
    props: {
        solicitud_recurso: { typeof: Object, default:() => {} }
    },
    data:() =>({
        reglas_clabe_interbancaria: [
            v => !!v || 'Clabe interbancaria es requerido.',
            v => v.length === 18 || 'Debería tener 18 dígitos.'
        ],
        reglas_clabe: [
            v => !!v || 'Clabe es requerido.',
            v => v.length === 16 || 'Debería tener 18 dígitos.'
        ],
        reglas_numero_cuenta: [
            v => !!v || 'Numero cuenta es requerido.',
            v => v.length === 10 || 'Debería tener 10 dígitos.'
        ],
        reglas_convenio: [
            v => !!v || 'Convenio es requerido.',
            v => v.length === 20 || 'Debería tener 18 dígitos.'
        ],
        reglas_referencia: [
            v => !!v || 'Referencia es requerido.',
            v => 20 <= v.length && v.length <= 30 || 'Debería tener 20 a 30 dígitos.'
        ],
        reglas_linea_captura: [
            v => !!v || 'Linea de captura es requerido.',
            v => v.length === 20 || 'Debería tener 18 dígitos.'
        ]
    }),
    computed: {
        tipo_transferencia (){
            let titulo_tipo_transferencia = 'No ha seleccionado un tipo de transferencia.'
            switch(this.solicitud_recurso['transferencia'].tipo){
                case 'transferencia':
                    titulo_tipo_transferencia = 'Transferencia interbancaria'
                    break
                case 'cuenta':
                    titulo_tipo_transferencia = 'Cuenta bancaria'
                    break
                case 'servicio':
                    titulo_tipo_transferencia = 'Servicio'
                    break
            }
            return titulo_tipo_transferencia
        }
    },
    // switch(nuevo_valor){
