<template>
    <v-card outlined class="mx-auto">
        <v-card-title class="grey lighten-2">Datos generales de la solicitud de recurso</v-card-title>
        <br>
        <v-card-text v-if="!is_pago_separado">
            <v-form v-model="valid" ref="formulario" lazy-validation>
                <formulario-datos-generales :solicitud_recurso="solicitud_recurso_v2"></formulario-datos-generales>
            </v-form>
        </v-card-text>
        <v-card-text v-if="is_pago_separado">
            <v-responsive class="overflow-y-auto" max-height="500">
                <v-stepper
                  v-model="e6"
                  vertical
                >
                    <v-form v-model="valid" ref="formulario" lazy-validation>
                        <div v-for="(solicitud_recurso, i) in solicitudes_recurso_v2" :key="i">
                            <v-stepper-step
                                :complete="e6 > i+1"
                                :rules="[()=>!solicitud_recurso.peticiones[0].error]"
                                editable
                                :step="i+1"
                            >
                                <b>{{ trabajos_seleccionados[i].concepto }}</b> ({{ trabajos_seleccionados[i].proyecto }})
                                <small>{{ trabajos_seleccionados[i].inmueble.nombre }} ({{ trabajos_seleccionados[i].inmueble.clave }})</small>
                            </v-stepper-step>
                    
                            <v-stepper-content :step="i+1">
                                <div v-if="!solicitud_recurso.peticiones[0].error">
                                    <formulario-datos-generales :solicitud_recurso="solicitud_recurso"></formulario-datos-generales>
                                </div>
                                <div v-else>
                                    <v-card outlined class="text-center ma-1" color="error lighten-3" >
                                        <v-card-text class="text-subtitle-2">Este trabajo ya se encuesta en una solicitud de recurso</v-card-text>
                                    </v-card>
                                </div>
                            </v-stepper-content>
                        </div>
                    </v-form>
                </v-stepper>
            </v-responsive>
        </v-card-text>
    </v-card>
</template>

<script>
import formularioDatosGenerales from '../../components/Recurso/FormularioDatosGenerales.vue' 
export default {
data:() =>({
    continuar_p4: false,
    e6: 1
}),
components: {
    'formulario-datos-generales':formularioDatosGenerales
},
computed: {
    beneficiarios(){ return this.$store.getters["beneficiario/getBeneficiarios"] },
    trabajos_seleccionados() { return this.$store.getters["trabajo/getTrabajosSeleccionados"]; },
    solicitudes_recurso_v2() { return this.$store.getters["recurso/getSolicitudesRecursoV2"] },
    solicitud_recurso_v2(){ return this.$store.getters['recurso/getSolicitudRecursoV2'] },
    is_pago_separado() { return this.$store.getters["recurso/getIsPagoSeparadoSeleccionado"] },
    valid: {
        get(){ return this.continuar_p4 },
        set(nuevoValor){
            if (this.$refs.formulario){
                let is_valido = this.$refs.formulario.validate()
                this.$emit('eventoContinuar', { 'validacion': is_valido})
            } else {
                this.$emit('eventoContinuar', { 'validacion': false})
            }
            this.continuar_p4 = nuevoValor
        }
    }
},
created(){
    this.inicializarBeneficiarios()
},
methods: {
    async inicializarBeneficiarios(){
      await this.$store.dispatch("beneficiario/obtenerBeneficiarios")
    },
}
};
</script>