<template>
    <v-dialog v-model="dialog" persistent max-width="800">
        <template v-slot:activator="{ on, attrs }">
        <v-btn class="ma-2" color="success" dark v-bind="attrs" v-on="on">
            <v-icon dark left> mdi-account-plus </v-icon> Agregar nuevo personal 
        </v-btn>
        </template>
            <v-card class="mx-auto" :loading="is_cargando" :disabled="is_cargando">
                <v-card-title class="grey lighten-2">
                    Agregar nuevo personal
                    <v-spacer></v-spacer>
                    <v-btn icon color="error" @click="cerrar()"><v-icon>mdi-close</v-icon></v-btn>
                </v-card-title>
                <v-card-text>
                    <v-form class="container" ref="form" lazy-validation>
                        <v-row dense>
                            <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                                <v-text-field
                                    v-model="usuario.nombre"
                                    :rules="[
                                        v => !!v || 'Se requiere el nombre',
                                        v => (v && v.length >= 3) || 'El nombre debe tener al menos 3 caracteres'
                                    ]"
                                    label="Nombre"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                                <v-text-field
                                    v-model="usuario.apellidos"
                                    label="Apellidos"
                                    :rules="[
                                        v => !!v || 'Se requiere el apellido',
                                        v => (v && v.length >= 3) || 'El apellido debe tener al menos 3 caracteres'
                                    ]"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                                <v-text-field
                                    v-model="usuario.nombre_usuario"
                                    label="Nombre usuario"
                                    :rules="[
                                        v => !!v || 'Se requiere el nombre del usuario',
                                        v => (v && v.length >= 3) || 'El nombre de usuario debe tener al menos 3 caracteres'
                                    ]"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="6" md="6" lg="6" xl="6">
                                <v-text-field
                                    v-model="usuario.correo"
                                    label="Correo"
                                    :rules="[
                                        v => !!v || 'Se requiere el correo electronico',
                                        v => (v && v.length >= 3) || 'El correo electronico debe tener al menos 3 caracteres',
                                        v => !(/.+@.+\..+/.test(v)) || 'Solo selecciona el nombre del dominio',
                                    ]"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="6" md="6" lg="6" xl="6">
                                <v-select
                                    v-model="dominio"
                                    :items="dominios"
                                    label="Dominios"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="6" md="6" lg="6" xl="6">
                                <v-text-field
                                    v-model="usuario.pwd"
                                    :append-icon="mostrar_pwd ? 'mdi-eye' : 'mdi-eye-off'"
                                    :rules="[
                                        v => !!v || 'Se requiere la contraseña',
                                        v => (v && v.length >= 8) || 'La contraseña debe tener al menos 8 caracteres'
                                    ]"
                                    :type="mostrar_pwd ? 'text' : 'password'"
                                    label="Contraseña"
                                    @click:append="mostrar_pwd = !mostrar_pwd"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="6" md="6" lg="6" xl="6">
                                <v-text-field
                                    v-model="verificar_pwd"
                                    type="password"
                                    label="Verificar contraseña"
                                    outlined
                                    dense
                                    :rules="[v => !!v || 'Se requiere verificación de la contraseña']"
                                    :error-messages="error_messages_verificar_pwd"
                                    filled
                                />
                            </v-col>
                            <v-col
                                style="border: 1px solid rgba(0, 0, 0, 0.3); border-radius: 4px; background: rgba(0, 0, 0, 0.06); padding-right: 12px; padding-left: 12px; padding-top: 0%; padding-bottom: 0%;}"
                                xs="12" sm="12" md="12" lg="12" xl="12"
                            >
                                <v-row v-if="privilegios.length" dense>
                                    <v-col v-for="privilegio of privilegios" :key="privilegio.id" xs="12" sm="12" md="6" lg="6" xl="6">
                                        <v-switch
                                            v-model="privilegio_usuario"
                                            :label="privilegio.nombre"
                                            inset
                                            :value="privilegio.id"
                                            @change="setPrivilegioPersonal(privilegio)"
                                        />
                                    </v-col>
                                </v-row>
                                <v-row v-else dense>
                                    <v-col>
                                        <span>No hay una lista de privilegios para asignar al usuario.</span>
                                    </v-col>
                                </v-row>
                            </v-col>
                            <!-- <v-col cols="12" md="6">
                                <v-checkbox
                                    v-model="is_personal"
                                    label="¿Es un miembro del personal?"
                                />
                            </v-col> -->
                        </v-row>
                    </v-form>
                </v-card-text>
                <v-card-actions>
                    <v-spacer />
                    <v-btn @click="cerrar()" color="red" class="ma-3" dark>Cerrar</v-btn>
                    <v-btn @click="crearMiembro()" color="green" dark>Agregar</v-btn>
                </v-card-actions>
            </v-card>
    </v-dialog>
</template>

<script>
export default {
    data: () => ({
        dominio: "@advr.mx",
        dialog: false,
        dominios: ["@advr.mx", "@gesscorpzero.com"],
        mostrar_pwd: false,
        is_personal: true,
        pwd: '',
        error_messages_verificar_pwd: '',
        privilegio_seleccionado: -1
    }),
    computed: {
        usuario(){ return this.$store.getters["usuario/getUsuario"] },
        is_cargando() { return this.$store.getters["usuario/getCargando"] },
        verificar_pwd:{
            get(){ return this.pwd },
            set(nuevoValor){
                this.pwd = nuevoValor
                this.error_messages_verificar_pwd = this.usuario.pwd === this.pwd ? '' : 'Las contraseñas no coinciden'
            }
        },
        privilegios() {
            return this.$store.getters["grupo/getGrupos"]
        },
        privilegio_usuario: {
            get(){ return this.usuario.grupos },
            set(nuevoValor){
                if(!Array.isArray(nuevoValor)){
                    if(this.usuario.grupos.includes(nuevoValor)){
                        let index = this.usuario.grupos.indexOf(nuevoValor)
                        this.usuario.grupos.splice(index, 1)
                    } else {
                        this.usuario.grupos.push(nuevoValor)
                    }
                }
            }
        }
    },
    methods: {
        async crearMiembro() {
            if (this.$refs.form.validate()) {
                let correo = this.usuario.correo
                this.usuario.correo = this.usuario.correo.concat(this.dominio)
                const { mensaje, estado, error } = await this.$store.dispatch("usuario/crearUsuario");

                this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
                if(!error){ this.cerrar() }
                else { this.usuario.correo = correo.concat(this.dominio)}
            }
        },
        cerrar() {
            this.$refs.form.reset();
            this.$refs.form.resetValidation();
            this.dialog = false;
            this.dominio = "@advr.mx"
        },
        setPrivilegioPersonal(privilegio){
            this.privilegio_usuario = privilegio.id
        }
    },
};
</script>
