<template>
    <v-card class="mx-auto" outlined>
        <v-card-title class="grey white--text"> Fondos necesarios: <v-spacer /> MX$ {{total}} </v-card-title>
        <v-card-text>
            <v-form ref="formulario" v-model="valid" lazy-validation>
                <v-responsive class="overflow-y-auto" max-height="500">
                    <v-data-table
                        :items="peticiones_verificadas"
                        :headers="cabecera"
                        :footer-props="{
                            itemsPerPageText: 'Filas por página',
                            itemsPerPageAllText: 'Todos',
                            pageText: ' p - Tp de n',
                        }"
                    >
                        <template v-slot:item.concepto="{index}">
                            <b>{{ trabajos_seleccionados[index].concepto }}</b> ({{ trabajos_seleccionados[index].proyecto }})
                        </template>
                        <template v-slot:item.inmueble="{index}">
                            {{ trabajos_seleccionados[index].inmueble.nombre }} ({{ trabajos_seleccionados[index].inmueble.clave }})
                        </template>
                        <template v-slot:item.monto="{item}">
                            <v-card v-if="!item.error" class="ma-1" width="200">
                                <v-text-field
                                v-model="item.monto"
                                label="Importe"
                                solo
                                dense
                                autofocus
                                prefix="$"
                                type="number"
                                placeholder="1.00"
                                step="0.1"
                                min="1"
                                :rules="[(v) => v>0 || 'Es necesario escribir el monto mayor a 0.00' ]"
                                />
                            </v-card>
                            <v-card v-else class="text-subtitle-2 text-center ma-1" color="error lighten-3" width="200">
                                {{item.error}}
                            </v-card>
                        </template>
                        <template v-slot:item.eliminar="{index}">
                            <v-btn icon @click="confirmarEliminacion(index)"><v-icon color="error">mdi-trash-can</v-icon></v-btn>
                        </template>
                    </v-data-table>
                </v-responsive>
            </v-form>
        </v-card-text>
    </v-card>
</template>

<script>
export default {
    props:{
        cabecera: { type: Array, default: () => {} }
    },
    data: () => ({
        continuar: true,
        peticion: null,
    }),
    computed: {
        trabajos_seleccionados() { return this.$store.getters['trabajo/getTrabajosSeleccionados']; },
        peticiones_verificadas() { return this.$store.getters['recurso/getPeticionesVerificadas'] },
        total() {
            var total = 0.0
            for(const peticion of this.peticiones_verificadas){
                total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
            }
            return new Number(total.toFixed(2)).toLocaleString('es-MX', { style: 'currency', currency: 'MXN' })
        },
        valid: {
            get(){ return this.continuar },
            set(nuevoValor){
                if(this.$refs.formulario){
                    this.$emit('eventoContinuar', {'validacion': this.$refs.formulario.validate()})
                    this.continuar = nuevoValor
                }
            }
        },
    },
    methods: {
        confirmarEliminacion(index){
            this.$store.commit('trabajo/deleteSeleccionado', index)
            this.$store.commit('recurso/deleteVerificarPeticionesVerificadas', index)
            this.$store.commit('recurso/deleteSolicitudesRecursoV2', index)
            // console.log(this.peticiones_verificadas)
        }
    },
};
</script>
