<template>
  <v-main>
    <v-row justify="center" dense>
      <v-col cols="12" sm="2" md="2" lg="2" xl="2" xs="2">
        <cliente />
      </v-col>
      <v-col cols="12" sm="8" md="8" lg="8" xl="8" xs="8">
        <v-tabs v-model="tabVista" color="primary">
          <v-tab>Trabajos</v-tab>
          <v-tab>Inmuebles</v-tab>
          <v-tab-item class="container">
            <trabajos />
          </v-tab-item>
          <v-tab-item class="container">
            <inmubles />
          </v-tab-item>
        </v-tabs>
      </v-col>
      <v-col cols="12" sm="2" md="2" lg="2" xl="2" xs="2">
        <v-card class="mx-auto">
          <v-card-title class="grey lighten-2">Últimos cambios</v-card-title>
          <v-card-text>
            <v-timeline>
              <v-timeline-item class="text-right">
                Se eliminó el recurso
              </v-timeline-item>
              <v-timeline-item>Se modificó la solicitud de recurso</v-timeline-item>
              <v-timeline-item>Se creó un nuevo inmuebles</v-timeline-item>
            </v-timeline>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-main>
</template>

<script>
import Cliente from "../views/cliente/Cliente.vue";
import ListarTrabajosPendientes from "../views/Trabajo/Trabajos.vue";
import Inmuebles from "../views/Inmueble/InmueblesGeneral.vue";

export default {
  components: {
    "cliente": Cliente,
    "trabajos": ListarTrabajosPendientes,
    "inmubles": Inmuebles
  },
  computed:{
    tabVista:{
      get(){ return this.$store.getters["general/getTabHomeVista"] },
      set(nuevoValor){
        this.$store.commit("general/setTabHomeVista", nuevoValor)
      }
    }
  }
};
</script>
