<template>
    <v-main>
        <v-row dense justify="center">
            <v-col cols="12" sm="8" md="8" lg="8" xl="8">
                <v-stepper v-model="e1">
                    <v-stepper-header>
                        <v-stepper-step :complete="e1 > 1" step="1">
                            Paso 1 <br />
                            Trabajos
                        </v-stepper-step>
            
                        <v-divider />
                
                        <v-stepper-step :complete="e1 > 2" step="2">
                            Paso 2 <br />
                            Importe
                        </v-stepper-step>
                        
                        <v-divider />
                        
                        <v-stepper-step :complete="e1 > 3" step="3">
                            Paso 3 <br />
                            Modalidad de pago
                        </v-stepper-step>
                        
                        <v-divider />
                        
                        <v-stepper-step :complete="e1 > 4" step="4">
                            Paso 4 <br />
                            Datos generales
                        </v-stepper-step>
                        
                        <v-divider />
                        
                        <v-stepper-step step="5">
                            Paso 5 <br />
                            Confirmacion
                        </v-stepper-step>
                    </v-stepper-header>
            
                    <v-stepper-items>
                        <v-stepper-content step="1">
                            <tabla-trabajos-usuario
                                :mostrar_seleccionados="true"
                                :is_peticion="false"
                                :headers="headers"
                            />
                            <br />
                            <v-btn
                                color="success"
                                block
                                @click="verificarPeticiones()"
                                :disabled="isTrabajosSeleccionados"
                            >
                            Continue
                            </v-btn>
                        </v-stepper-content>
                
                        <v-stepper-content step="2">
                            <v-btn color="warning" block @click="e1 = 1"> Regresar </v-btn>
                            <br>
                            <formulario-importe-peticiones @eventoContinuar="setValidP2" :cabecera="cabecera"/>
                            <br>
                            <v-btn color="success" block @click="e1 = 3" :disabled="!isValid_p2">
                            Continuar
                            </v-btn>
                        </v-stepper-content>
                        
                        <v-stepper-content step="3">
                            <v-btn color="warning" block @click="e1 = 2"> Regresar </v-btn>
                            <br>
                            <formulario-crear-recurso @eventoContinuar="setValidP3"></formulario-crear-recurso>
                            <br>
                            <v-btn color="success" block @click="e1 = 4" :disabled="!isValid_p3">
                            Continuar
                            </v-btn>
                        </v-stepper-content>
                
                        <v-stepper-content step="4">
                            <v-btn color="warning" block @click="e1 = 3"> Regresar </v-btn>
                            <br>
                            <datos-generales @eventoContinuar="setValidP4" />
                            <br>
                            <v-btn
                                color="success"
                                block
                                :disabled="!isValid_p4"
                                @click="asignarPeticionesSolicitudRecurso()"
                            > Continue </v-btn>
                        </v-stepper-content>
                
                        <v-stepper-content step="5">
                            <v-btn color="warning" block @click="e1 = 4"> Regresar </v-btn>
                            <br>
                            <crear-recurso />
                            <br>
                            <v-btn color="primary" block @click="crearSolicitudRecurso()"> Finalizar  </v-btn>
                        </v-stepper-content>
                    </v-stepper-items>
                </v-stepper>
            </v-col>
            <!-- <v-col cols="12" sm="2" md="2" lg="2" xl="2">
                <v-card class="mx-auto" outlined>
                    <v-toolbar dense elevation="1"><v-toolbar-title>Resumen de la solicitud de recursos </v-toolbar-title></v-toolbar>
                    <v-card-text>
                    Trabajos: <div >
                        <v-chip
                          class="ma-1"
                          v-for="trabajo in trabajos" :key="trabajo.id"
                        >
                          {{trabajo.concepto}}({{ trabajo.proyecto }})
                        </v-chip></div> <br>
                    Innmuebles: ... <br>
                    Fecha: {{getFechaMod()}} <br>
                    Pagar por separado: {{solicitud_recurso_v2.pago_separado ? 'Si' : 'No'}} <br>
                    </v-card-text>
                    <v-divider  />
                    <v-card-title>Total: {{ total }}</v-card-title>
                    <div class="text-end">
                    <v-btn rounded color="error" @click="$router.go(-1)">
                        Cancelar
                        <v-icon right>mdi-close-circle-outline</v-icon>
                    </v-btn>
                    </div>
                </v-card>
            </v-col> -->
        </v-row>
    </v-main>
</template>

<script>
import TablaTrabajos from '../../components/trabajo/TablaTrabajos.vue'
import FormularioImportePeticiones from '../../components/Recurso/FormularioImporte.vue'
import DatosGenerales from '../../components/Recurso/DatosGenerales.vue'
import FormularioSolicitudRecuro from '../../components/Recurso/ModalidadPago.vue'
import CrearRecurso from '../../components/Recurso/ComprobacionSolicitudRecurso.vue'

export default {
    data: () => ({
        e1: 1,
        mostrar_seleccionados: true,
        isValid_p2: false,
        isValid_p3: false,
        isValid_p4: false,
        headers: [
            { text: "Proyecto", value: "proyecto" },
            { text: "Concepto", value: "concepto" },
            { text: "Status", value: "status" },
            { text: "Inmueble", value: "inmueble.nombre" },
            { text: "Clave", value: "inmueble.clave" },
            { text: "Estado", value: "inmueble.estado" },
            { text: "Municipio", value: "inmueble.municipio" },
            { text: "Asignación", value: "creado" },
            { text: "Actualizado", value: "modificado" },
        ],
        cabecera: [
            {'text': 'Concepto del trabajo', 'value': 'concepto'},
            {'text': 'Inmueble', 'value': 'inmueble'},
            {'text': 'Importe', 'value': 'monto'},
            {'text': 'Eliminar', 'value': 'eliminar'}
        ]
    }),
    components: {
        "tabla-trabajos-usuario": TablaTrabajos,
        'formulario-importe-peticiones': FormularioImportePeticiones,
        "formulario-crear-recurso": FormularioSolicitudRecuro,
        'datos-generales': DatosGenerales,
        "crear-recurso": CrearRecurso
    },
    computed: {
        trabajos() { return this.$store.getters["trabajo/getTrabajos"]; },
        isTrabajosSeleccionados() { return this.$store.getters["trabajo/getTrabajosSeleccionados"].length ? false : true },
        verificar_peticiones(){return this.$store.getters["recurso/getVerificarPeticiones"]},
        isPeticiones_verificadas() { return this.$store.getters["recurso/getPeticionesVerificadas"].length ? true : false },
        solicitud_recurso(){ return this.$store.getters["recurso/getSolicitudRecurso"] },
        solicitud_recurso_v2(){ return this.$store.getters["recurso/getSolicitudRecursoV2"] },
        total(){
            var total = 0.0
            for(const peticion of this.$store.getters["recurso/getPeticionesVerificadas"]){
                total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
            }
            return new Number(total.toFixed(2)).toLocaleString("es-MX", { style: "currency", currency: "MXN" })
        },
    },
    methods:{
        setValidP2({validacion}){ this.isValid_p2 = validacion },
        setValidP3({validacion}){ this.isValid_p3 = validacion },
        setValidP4({validacion}){ this.isValid_p4 = validacion },
        async verificarPeticiones() {
            const { mensaje, estado, error } = await this.$store.dispatch("recurso/verificarPeticiones", this.verificar_peticiones)
            this.$store.commit('recurso/setIsPagoSeparadoSeleccionado', false)
            this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
            this.e1 = 2
        },
        getFechaMod(){
            let date= new Date();
            return date.toLocaleDateString();
        },
        async asignarPeticionesSolicitudRecurso(){
            this.$store.dispatch('recurso/inicializarAsignacionPeticionesSolicitudRecurso')
            this.e1 = 5
        },
        async crearSolicitudRecurso(){
            const {mensaje, estado, error} = await this.$store.dispatch("recurso/crearRecurso")
            if(!error){
                this.$store.commit("trabajo/clearSeleccionados")
                this.$router.push({ 'name': 'recursos-usuario' })
            }
            this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        }
    }
}
</script>