<template>
    <v-card outlined class="mx-auto">
        <v-card-title class="grey lighten-2">Modalidad de pago</v-card-title>
        <v-card-title class="warning lighten-4">
            <v-switch
                v-model="is_pago_por_separado"
                :label="`¿Efectuar el pago por separado? ${is_pago_por_separado ? 'SI' : 'NO'}`"
                inset
            />
        </v-card-title>
        <br>
        <v-card-text v-if="!is_pago_por_separado">
            <v-form v-model="valid" ref="formulario" lazy-validation>
                <v-row dense>
                    <v-col cols="12" sm="6">
                        <v-select
                            v-model="modalidad_pago"
                            :items="modalidades_pago"
                            item-text="nombre"
                            item-value="value"
                            label="Modalidad de pago"
                            :rules="[(v) => !!v || 'Es necesario elegir una modalidad de pago']"
                            outlined
                            clearable
                            filled
                        />
                    </v-col>
                    <v-col cols="12" sm="6" v-if="modalidad_pago === 'transferencia'">
                        <v-select
                            v-if="modalidad_pago === 'transferencia'"
                            v-model="tipo_transferencia"
                            :rules="[(v) => !!v || 'Es necesario elegir un tipo de transferencia']"
                            label="Tipo transferencia"
                            :items="tipos_transferencia"
                            item-text="nombre"
                            item-value="valor"
                            outlined
                            clearable
                            filled
                        />
                    </v-col>
                </v-row>
            </v-form>
        </v-card-text>
        <v-card-text v-else>
            <v-responsive class="overflow-y-auto" max-height="400">
                <v-form ref="formulario" v-model="valid" lazy-validation>
                    <v-responsive class="overflow-y-auto" max-height="500">
                        <v-data-table
                            :items="solicitudes_recurso_v2"
                            :headers="cabecera"
                            :footer-props="{
                                itemsPerPageText: 'Filas por página',
                                itemsPerPageAllText: 'Todos',
                                pageText: ' p - Tp de n'
                            }"
                        >
                            <template v-slot:item.concepto="{index}">
                                <b>{{ trabajos_seleccionados[index].concepto }}</b> ({{ trabajos_seleccionados[index].proyecto }})
                            </template>
                            <template v-slot:item.inmueble="{index}">
                                {{ trabajos_seleccionados[index].inmueble.nombre }} ({{ trabajos_seleccionados[index].inmueble.clave }})
                            </template>
                            <template v-slot:item.modalidad_pago="{item}">
                                <v-card v-if="!item.peticiones[0].error" class="ma-1" width="200">
                                    <v-select
                                        v-model="item.modalidad_pago"
                                        :items="modalidades_pago"
                                        item-text="nombre"
                                        item-value="value"
                                        label="Modalidad de pago"
                                        :rules="[(v) => !!v || 'Es necesario elegir una modalidad de pago']"
                                        outlined
                                        clearable
                                        filled
                                    />
                                </v-card>
                                <v-card v-else class="text-subtitle-2 text-center ma-1" color="error lighten-3" width="200">
                                    {{item.peticiones[0].error}}
                                </v-card>
                            </template>
                            <template v-slot:item.tipo_pago="{item}">
                                <v-card v-if="!item.peticiones.error && item.modalidad_pago === 'transferencia'" icon width="200">
                                    <v-select
                                        v-model="item['transferencia'].tipo"
                                        :rules="[(v) => !!v || 'Es necesario elegir un tipo de transferencia']"
                                        label="Tipo transferencia"
                                        :items="tipos_transferencia"
                                        item-text="nombre"
                                        item-value="valor"
                                        outlined
                                        clearable
                                        filled
                                    />
                                </v-card>
                                <!-- <v-card v-if="!item.peticiones.error" icon width="200">
                                    <v-text-field
                                        v-model="item['transferencia'].tipo" solo dense autofocus disabled :rules="[(v) => !!v || 'Seleccione un método de pago']" type="number">
                                        <template v-slot:append-outer>
                                            <v-icon color="success" @click="activarDialogFormTipoTransferencia(item)">mdi-credit-card-plus</v-icon>
                                        </template>
                                    </v-text-field>
                                </v-card> -->
                            </template>
                            <template v-slot:item.eliminar="{index}">
                                <v-btn icon @click="confirmarEliminacion(index)"><v-icon color="error">mdi-trash-can</v-icon></v-btn>
                            </template>
                        </v-data-table>
                    </v-responsive>
                </v-form>
            </v-responsive>
        </v-card-text>
    </v-card>
</template>

<script>
export default {
data:() =>({
    continuar_p4: false,
    is_formulario_tipo_transferencia: false,
    modalidades_pago: [{nombre: 'Transferencia', value: 'transferencia'}, {nombre: 'Cheque', value: 'cheque'}],
    tipos_transferencia: [
        {nombre: 'Transferencia', valor: 'transferencia'},
        {nombre: 'Cuenta', valor: 'cuenta'},
        {nombre: 'Servicio', valor: 'servicio'}
    ],
    cabecera: [
        {'text': 'Trabajo', 'value': 'concepto'},
        {'text': 'Inmueble', 'value': 'inmueble'},
        {'text': 'Modalidad pago', 'value': 'modalidad_pago'},
        {'text': 'Tipo de pago', 'value': 'tipo_pago'},
        {'text': 'Eliminar', 'value': 'eliminar'}
    ],
    // solicitud_recurso: {}
}),
computed: {
    trabajos_seleccionados(){ return this.$store.getters["trabajo/getTrabajosSeleccionados"]; },
    solicitudes_recurso_v2(){ return this.$store.getters["recurso/getSolicitudesRecursoV2"] },
    solicitud_recurso_v2(){ return this.$store.getters['recurso/getSolicitudRecursoV2'] },
    valid: {
        get(){ return this.continuar_p4 },
        set(nuevoValor){
            if (this.$refs.formulario){
                let is_valido = this.$refs.formulario.validate()
                this.$emit('eventoContinuar', { 'validacion': is_valido})
            } else {
                this.$emit('eventoContinuar', { 'validacion': false})
            }
            this.continuar_p4 = nuevoValor
        }
    },
    modalidad_pago: {
        get(){ return this.solicitud_recurso_v2.modalidad_pago },
        set(nuevoValor){
            this.$store.commit('recurso/setModalidadPagoSolicitudRecurso', nuevoValor)
        }
    },
    tipo_transferencia:{
        get(){ return this.solicitud_recurso_v2['transferencia'].tipo },
        set(nuevoValor){
            this.$store.commit('recurso/setTipoTransferenciaSolicitudRecurso', nuevoValor)
        }
    },
    is_pago_por_separado:{
        get() { return this.$store.getters['recurso/getIsPagoSeparadoSeleccionado'] },
        set(nuevoValor){
            if(nuevoValor){ this.$store.dispatch('recurso/inicializarAsignacionPeticionesSolicitudesRecurso', nuevoValor)}
            this.$store.commit('recurso/setIsPagoSeparadoSeleccionado', nuevoValor)
        }
    }
},
methods: {
    // activarDialogFormTipoTransferencia(solicitud_recurso){
    //     this.solicitud_recurso = solicitud_recurso
    //     this.is_formulario_tipo_transferencia = true
    // }
    confirmarEliminacion(index){
        this.$store.commit('trabajo/deleteSeleccionado', index)
        this.$store.commit('recurso/deleteVerificarPeticionesVerificadas', index)
        this.$store.commit('recurso/deleteSolicitudesRecursoV2', index)
    }
}
};
</script>