<template>
    <v-row dense justify="center">
        <v-col cols="12" sm="8">
            <v-row>
                <v-col cols="12" sm="10" xs="10">
                    <v-btn-toggle v-model="vista_trabajo" color="primary">
                        <v-btn  value="asignado">
                            <span class="hidden-sm-and-down">Asignado</span>
                        </v-btn>
                        <v-btn value="creado">
                            <span class="hidden-sm-and-down">Creado</span>
                        </v-btn>
                    </v-btn-toggle>
                </v-col>
            </v-row>
        </v-col>
        <v-col cols="12" sm="8">
            <tabla-trabajos-usuario
                :is_peticion="vista_trabajo === 'asignado' ? true : false"
                :headers="headers"
                :objAsignar="{isAsignarBtn: true, deshabilitar: false, redireccionar: 'asignar-trabajos'}"
            />
        </v-col>
    </v-row>
</template>

<script>
import TablaTrabajos from '../../components/trabajo/TablaTrabajos.vue'
export default {
  data: () => ({
    headers: [
      { text: "Proyecto", value: "proyecto" },
      { text: "Concepto", value: "concepto" },
      { text: "Status", value: "status" },
      { text: "Inmueble", value: "inmueble.nombre" },
      { text: "Estado", value: "inmueble.estado" },
      { text: "Municipio", value: "inmueble.municipio" },
      { text: "Creado", value: "creado" },
      { text: "Actualizado", value: "modificado" },
    ],
    vista: "asignado",
  }),
  components: {
    "tabla-trabajos-usuario": TablaTrabajos,
  },
  created() {
    this.inicializarTrabajos({username: this.username, usuario_area: null});
  },
  computed: {
    username() { return this.$store.getters["MOD_autenticacion/getNombreUsuario"] },
    vista_trabajo:{
      get(){ return this.vista },
      set(nuevoValor){
        switch(nuevoValor){
          case 'creado':
            this.inicializarTrabajos({username: null, usuario_area: this.username})
            break
            case 'asignado':
              this.inicializarTrabajos({username: this.username, usuario_area: null})
            break
        }
        this.vista = nuevoValor
      }
    }
  },
  methods: {
    async inicializarTrabajos({username, usuario_area}) {
      this.$store.commit("trabajo/cleanTrabajos")
      const { mensaje, estado, error } = await this.$store.dispatch( "trabajo/obtenerTrabajosUsuario",{username, usuario_area});
      if (error) {
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      }
    },
  },
};
</script>

<style>
span {
  text-transform: none !important;
}
tbody tr:nth-of-type(odd) {
  background-color: rgba(76, 129, 253, 0.125);
}
</style>
