<template>
  <v-menu v-model="menuVistas" offset-y left :close-on-content-click="false">
    <template v-slot:activator="{ on: menu, attrs }">
      <v-tooltip bottom>
        <template v-slot:activator="{ on: tooltip }">
          <v-avatar text v-bind="attrs" v-on="{ ...tooltip, ...menu }">
            <v-icon>mdi-plus</v-icon></v-avatar>
        </template>
        <span>Ir a...</span>
      </v-tooltip>
    </template>
    <v-card outlined>
      <v-list>
        <v-list-group v-for="(vista, index) in vistas" :key="index">
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>{{ vista.titulo }}</v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item v-for="(metodo, index) in vista.metodos" :key="index">
            <v-list-item-content @click="goVista(vista.titulo, metodo.valor)">
              <v-list-item-subtitle>{{ metodo.titulo }}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </v-list>
    </v-card>
  </v-menu>
</template>

<script>
export default {
  data: () => ({
    menuVistas: false,
    vistas: [
      {
        titulo: "Cliente",
        metodos: [{titulo: "Crear nuevo cliente", valor: 1}]
      },
      {
        titulo: "Inmueble",
        metodos: [{titulo: "Ver inmuebles", valor: 1},{titulo: "Crear multiples inmuebles", valor: 2}]
      },
      {
        titulo: "Trabajo",
        metodos: [
          {titulo: "Asignar trabajos"},
          {titulo: "Ver mis trabajos"},
          {titulo: "Ver trabajos creados"}
        ]
      },
      {
        titulo: "Recursos",
        metodos: [
          {titulo: "Crear solicitud de recurso"},
          {titulo: "Ver mis recursos"},
          {titulo: "Ver recursos creados"}
        ]
      },
      {
        titulo: "Pagos",
        metodos: [
          {titulo: "Ver pagos"},
        ]
      },
    ],
  }),
  methods:{
    setDialog({isDialogo}){
      this.is_dialogo_formulario_crear_nuevo_inmueble = isDialogo
    },
    goVista(vista, metodo){
      switch(vista){
        case 'Cliente':
          this.goClienteMetodo(metodo)
          break
        case "Inmueble":
          this.goInmuebleMetodo(metodo)
          break
        default:
          break
      }
    },
    goClienteMetodo(metodo){
      switch(metodo){
        case 1:
          console.log(metodo)
          this.$store.commit("cliente/setIsActivoFormularioCrearNuevoCliente", true)
          break
        default:
          break
      }
    },
    goInmuebleMetodo(metodo){
      switch(metodo){
        case 1:
          this.$store.commit("general/setTabHomeVista", 1)
          // this.$store.commit("inmueble/setIsDialogoFormularioCrearNuevoInmuble", true)
          break
        default:
          break
      }
    }
  }
};
</script>